'use client';

import { useAuth } from '@/context/AuthContext';
import { useRouter, usePathname } from 'next/navigation';
import { useEffect } from 'react';
import { useLocale } from 'next-intl';

export default function AdminLayout({ children }: { children: React.ReactNode }) {
    const { user, loading } = useAuth();
    const router = useRouter();
    const pathname = usePathname();
    const locale = useLocale();

    useEffect(() => {
        if (!loading && !user && !pathname.includes('/login')) {
            router.push(`/${locale}/admin/login`);
        }
    }, [user, loading, router, pathname, locale]);

    if (loading) return <div className="flex h-screen items-center justify-center">Loading...</div>;

    // Allow rendering logic page even without user (it handles redirect internally if already logged in)
    if (!user && pathname.includes('/login')) {
        return <>{children}</>;
    }

    if (!user) return null;

    return (
        <div className="min-h-screen flex flex-col">
            <header className="border-b p-4 flex justify-between items-center bg-background">
                <h1 className="text-xl font-bold">Admin Dashboard</h1>
                <div className="flex items-center gap-4">
                    <span className="text-sm text-muted-foreground">{user.email}</span>
                </div>
            </header>
            <main className="flex-1 p-8 bg-muted/10">
                {children}
            </main>
        </div>
    );
}
